.inputwrapper {
  width: 100%;
  height: 3.5rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.timerinput {
  height: 3.5rem;
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.timerbutton {
  color: #fff;
  cursor: pointer;
  height: 3.5rem;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-left: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

.timerbutton:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

.timer {
  justify-content: center;
  display: flex;
}

.field {
  margin: 0 10px;
}

.value {
  color: #333;
  font-size: 24px;
  font-weight: bold;
}

.label {
  color: #666;
  font-size: 14px;
}

.timer, .inputwrapper, .timerinput, .timerbutton {
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.timerbutton {
  background: linear-gradient(to right, #007bff, #0056b3);
}

.timerbutton:hover {
  background: linear-gradient(to right, #0056b3, #007bff);
  transition: background .3s ease-in-out;
}

.timerinput:focus, .timerbutton:focus {
  outline: none;
}

